.btm_fixed {
  display: block;
  position: fixed;
  right: 10px;
  bottom: 100px; }

.custom_img {
  width: 100%;
  background-color: #fff; }

.custom_img_top {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.z_container {
  width: 100%;
  height: 100%;
  overflow: auto;
  clear: both; }

.z_photo {
  overflow: auto;
  clear: both; }

.z_photo img {
  width: 60px;
  height: 60px; }

.z_addImg {
  float: left;
  margin-right: 12px;
  border: 1px solid #efefef; }

.z_file {
  width: 60px;
  height: 60px;
  background: url(../img/upload_img.png) no-repeat;
  background-size: 100% 100%;
  float: left;
  margin-right: 12px; }

.z_file input::-webkit-file-upload-button {
  width: 60px;
  height: 60px;
  border: none;
  position: absolute;
  outline: 0;
  opacity: 0; }

.z_file input#file {
  display: block;
  width: auto;
  border: 0;
  vertical-align: middle; }

/*遮罩层*/
.z_mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: none; }

.z_alert {
  width: 180px;
  height: 120px;
  border-radius: 12px;
  background: #fff;
  font-size: 14px;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -90px;
  margin-top: -120px; }

.z_alert p:nth-child(1) {
  line-height: 90px; }

.z_alert p:nth-child(2) span {
  display: inline-block;
  width: 49%;
  height: 30px;
  line-height: 30px;
  float: left;
  border-top: 1px solid #ddd; }

.z_cancel {
  border-right: 1px solid #ddd; }
