/*城市弹层*/
.city-container {
	width: 100%;
	position: absolute;
	top: 56px;
	left: 0;
	background-color: #ffffff;
	z-index: 9999;
	display: none;
}

.letter-list {
	width: 5%;
	overflow: hidden;
	line-height: 32px;
	font-size: 16px;
	position: fixed;
	top: 20px;
	right: 10px;
	text-align: center;
}

.letter-list ul {
	list-style-type: none;
}

.letter-list ul li a {
	text-decoration: none;
}

.city-box {
	width: 90%;
	overflow: hidden;
	padding: 20px;
}

.city-list {
	width: 100%;
	overflow: hidden;
}

.city-list .city-letter {
	color: #6e6e6e;
	font-size: 16px;
	display: inline-block;
	padding-top: 15px;
	padding-bottom: 5px;
	border-bottom: 1px solid #e8ecf1;
	width: 100%;
}

.city-list p {
	color: #afafaf;
	width: 95%;
	height: 50px;
	line-height: 50px;
	border-bottom: 1px solid #e8ecf1;
	cursor: pointer;
}

.UISelect {
	margin-top: 5px
}